<template>
  <div class="tabs-component">
    <ul class="tabs-component-tabs">
      <li
        v-for="(tab, i) in tabs"
        :key="i"
        :class="{ 'is-active': tab.path === currentPath }"
        class="tabs-component-tab"
      >
        <router-link :to="tab.path" class="tabs-component-tab-a">{{tab.title}}</router-link>
      </li>
    </ul>
    <div class="tabs-component-panels">
      <slot/>
    </div>
  </div>
</template>

<script>
    export default {
        props: {
            tabs: {
                type: Object,
                default: () => {}
                },
        },
        
        computed: {
            currentPath() {
                return this.$route.path;
            }
        },
    };
</script>
