<template>
  <div class="acc-info">

    <!-- Loading Overlay -->
    <loading :active.sync="isLoading"
             :is-full-page="false"
             color="#0060f1"
             loader="dots">
    </loading>

    <!-- User -->
    <div class="user">
      <div class="user__left">
        <img class="user__avatar" src="@/assets/img/account/ava.png" alt="avatar">
        <div class="status-icon" :class="{ 'status-icon--success': status === 'done' }">
          <img v-if="status === 'done'" src="@/assets/img/account/v.svg" alt="status success">
          <img v-else src="@/assets/img/account/warning.svg" alt="status warning">
        </div>
      </div>
      <div class="user__right">
        <div class="user__right__name" v-if="profile.fields">
          {{profile.fields.firstname}} {{profile.fields.surname}}
        </div>
        <div class="user__right__id" v-if="profile.fields">
          ID {{profile.fields.userID}}
        </div>
      </div>
    </div>

    <div class="block__line"></div>
      <div class="account-status">
        <div class="account-status__title">
          {{statusMessages[status]}}
        </div>
        <router-link :to="statusButtons[status].link">
          <button :name="statusButtons[status].title" class="btn btn__outline completed-info__btn">
            {{statusButtons[status].title}}
          </button>
        </router-link>
      </div>
    <div class="block__line"></div>

    <!-- Balance-->
    <div class="total-balance">
      <div class="total-balance__left">Total balance</div>
      <div class="total-balance__right" v-if="profile.fields">
        {{totalBalance}} {{profile.fields.currency}}
      </div>
    </div>

    <div class="balance" v-for="account in balance.accounts" :key="account.name">
      <div class="balance__left" v-if="account.isBonusAccount">
        {{account.name}} bonus balance
      </div>
      <div class="balance__left" v-else>
        {{account.name}} account balance
      </div>
      <div class="balance__right" v-if="profile.fields">
        {{account.amount}} {{profile.fields.currency}}
      </div>
    </div>

    <router-link :to="{name: 'account', params: { tab: 'deposit-money' }}">
      <button type="button" class="btn btn--with-btn deposit-btn">
        Make deposit
        <span class="btn-in-btn">
          <img src="@/assets/img/svg/triangle.svg" alt="Arrow">
        </span>
      </button>
    </router-link>

  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';
  import subscribe from "@/services/subscribeService";

	export default {
		name: "AccountInfo",
    data() {
      return {
        isLoading: false,
        statusMessages: {
          edit    : 'Your account is not filled',
          verify  : 'Your account requires verification',
          done    : 'Your account is active'
        },

        statusButtons: {
          edit    : {title: 'Fill your account', link: '/account/personal-info'},
          verify  : {title: 'Verify your account', link: '/account/personal-info'},
          done    : {title: 'Go to games', link: '/link to games'},
        }
      }
    },
		methods: {
      ...mapActions(['GET_GAMING_ACCOUNTS']),
		},
    computed: {
      ...mapGetters({
        'profile' : 'getProfile',
        'status'  : 'getProfileStatus',
        'balance' : 'getProfileBalance'
      }),
      totalBalance() {
        return (this.balance.accounts)
          ? this.balance.accounts.reduce((total, account) => total + account.amount, 0)
          : 'N/A';
      }
    },
    async mounted() {
      this.isLoading = true;
      await this.GET_GAMING_ACCOUNTS();
      if (this.profile.fields) {
        console.log(this.profile.fields.userID);
        subscribe.balanceChanged({id: this.profile.fields.userID});
      }
      this.isLoading = false;
    }
	}
</script>

<style lang="sass" scoped>
  .user
    display: flex
    justify-content: flex-start
    align-items: center
    margin-bottom: rem(25)
    &__avatar
      padding: rem(4)
      border: solid rem(2) #F3F4F5
      border-radius: 50%
    &__left
      display: flex
      justify-content: center
      align-items: center
      margin-right: rem(20)
      position: relative
    &__right
      display: flex
      flex-direction: column
      justify-content: center

      &__name
        color: #000521
        font-size: rem(24)

      &__id
        font-size: rem(14)
        color: #636E7E
  .status-icon
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    width: rem(23)
    height: rem(23)
    top: 0
    right: 0
    background: #F13F35
    border-radius: 50%
    &--success
      background: #86C339



  .total-balance
    display: flex
    justify-content: space-between
    align-items: center
    color: #000521
    margin: rem(25) 0 rem(30)

    &__left
      font-size: rem(16)
      font-weight: 500

    &__right
      font-size: rem(24)
      font-weight: 500

  .balance
    display: flex
    justify-content: space-between
    color: #636E7E
    align-items: center
    margin: rem(15) 0

    &__left
      font-size: rem(14)
      color: #636E7E

    &__right
      font-size: rem(16)
      color: #000521

  .deposit-btn
    margin: rem(30) 0 rem(10)

  .account-status
    margin: rem(30)

    &__title
      font-size: rem(14)
      color: #636E7E
      margin-bottom: rem(10)

</style>
