<template>
  <div class="account">
    <div class="container">
      <br>
      <div class="tabs-panel">
        <Tabs :tabs = 'tabs' />
        
        <div class="tabs-component-page">
          <LeftBlock >
            <component :is="currentTab" v-if="currentTab"></component>
          </LeftBlock>

          <RightBlock class="block">
            <account-info></account-info>
          </RightBlock>
        </div>
        
        <button class="log-out" @click="logOut">Log Out</button>
      </div>
    </div>
  </div>
</template>

<script>
  import Tabs from '@/components/Tabs';
  import call from '@/services/callService';
  import RightBlock from "@/components/Account/RightBlock";
  import LeftBlock from "@/components/Account/LeftBlock";
  import AccountInfo from "@/components/Account/elements/accountInfo/AccountInfo";
  import {mapGetters, mapMutations, mapActions} from 'vuex';

  export default {
    name: "Account",
    data() {
      return {
        newBoolean: true,
        newVar: '',
        currentLang: {
          name: 'En'
        },
        langs: [{name: 'En'}, {name: 'Sk'}, {name: 'Ua'}],
        tabs: {
            'personal-info'       : {title: 'PersonalInfo'     , path: '/account/personal-info'},
            'deposit-money'       : {title: 'Deposit money'      , path: '/account/deposit-money'},
            'withdraw'            : {title: 'Withdraw'           , path: '/account/withdraw'},
            'bonuses'             : {title: 'Bonuses'            , path: '/account/bonuses'},
            'vip'                 : {title: 'VIP'                , path: '/account/vip'},
            'transaction-history' : {title: 'Transaction History', path: '/account/transaction-history'},
            'responsible-gaming'  : {title: 'Responsible Gaming' , path: '/account/responsible-gaming'},
        }
      };
    },
    methods: {
      ...mapMutations([
        'RESET_USER',
        'RESET_SESSION',
      ]),
      ...mapActions([
        'GET_USER_PROFILE'
      ]),
      updateLang(val) {
        this.currentLang = val;
      },
      logOut() {
        if (this.sessionInfo.isAuthenticated) {
          call.userLogout();
        } else {
          console.log('not found session');
          this.$router.push('/');
        }
      },
      checkCorrectTab(tab) {
        if (!this.tabs[tab] || tab === '/' ) {
            this.$router.push('/account/personal-info');
        }
      },
      async loadingProfile() {
        try {
          this.isLoading = true;
          await this.GET_USER_PROFILE();
          this.isLoading = false;
        }
        catch (err) {
          const message = err.kwargs.desc || JSON.stringify(err);
          this.$notify({
            group: 'app',
            type:'error',
            text: message
          });
          console.error(`EM Get Profile Error: ${message}`);
        }
      },
    },
      watch:{
        $route (to){
          this.checkCorrectTab(to.params.tab);
        }
      },
      created() {
        this.checkCorrectTab(this.$route.params.tab);
      },
    computed: {
      ...mapGetters({
        sessionInfo: 'getSessionInfo'
      }),
      currentTab() {
        return this.$route.params.tab;
      }
    },
    components: {
      LeftBlock,
      RightBlock,
      AccountInfo,
      Tabs,
      PersonalInfo:  () => import('@/components/Account/list/PersonalInfo'),
      DepositMoney: () => import('@/components/Account/list/DepositMoney'),
      Withdraw:  () => import('@/components/Account/list/Withdraw'),
      Bonuses: () => import('@/components/Account/list/Bonuses'),
      Vip: () => import('@/components/Account/list/VIP'),
      TransactionHistory: () => import('@/components/Account/list/TransactionHistory'),
      ResponsibleGaming: () => import('@/components/Account/list/ResponsibleGaming'),
    },
    async mounted() {
      await this.loadingProfile();
    }
  }
</script>

<style lang="sass" scoped>
  .account
    background-color: #F3F4F5
    .mark
      color: black
    .buttons_this_comp_style
      display: flex
      justify-content: center
    .btn_this_comp_style
      margin: 25px 10px 5px
  .tabs-panel
    position: relative
  .log-out
    display: flex
    justify-content: flex-end
    align-items: center
    font-size: 14px
    width: 80px
    position: absolute
    top: rem(12)
    right: 0
    color: black
    cursor: pointer

    &:after
      content: ''
      width: 20px
      height: 30px
      position: relative
      background: url('../assets/img/svg/account_tabs/tab_log_out.svg') center center no-repeat
      margin-left: 3px

  $colorTabText: #636E7E
  $colorTabTextHover: #000521
  $colorBlockTitle: #a2a8b2

  @mixin tabs-link-before
    content: ''
    width: 20px
    height: 20px
    position: relative
    margin-right: 10px

  // -- Tabs

  /deep/ .tabs-component
    display: flex
    position: relative
    align-items: center
    flex-direction: column
    &-tabs
      display: flex
      flex-grow: 1
      flex-shrink: 0
      justify-content: flex-start
      align-items: center
      border-bottom: 1px solid #E0E0E0
    &-panels
      width: 100%
    &-tab
      margin-right: 2rem

      // link a styles

      &-a
        position: relative
        display: flex
        justify-content: center
        font-size: 14px
        align-items: center
        color: $colorTabText
        padding: .8rem 0
        vertical-align: top
        transition: .1s ease-out

        &:hover
          color: $colorTabTextHover

      // active bottom line styles

      &.is-active
        .tabs-component-tab-a
          color: $colorTabTextHover

          &:after
            content: ''
            position: absolute
            width: 100%
            height: rem(4)
            bottom: 0
            background: $colorMain
            border-top-left-radius: rem(20)
            border-top-right-radius: rem(20)

      &:nth-child(1)
        .tabs-component-tab-a
          &:before
            @include tabs-link-before
            background: url('../assets/img/svg/account_tabs/tab_svg_1.svg') center center no-repeat

      &:nth-child(2)
        .tabs-component-tab-a
          &:before
            @include tabs-link-before
            background: url('../assets/img/svg/account_tabs/tab_svg_2.svg') center center no-repeat

      &:nth-child(3)
        .tabs-component-tab-a
          &:before
            @include tabs-link-before
            background: url('../assets/img/svg/account_tabs/tab_svg_3.svg') center center no-repeat

      &:nth-child(4)
        .tabs-component-tab-a
          &:before
            @include tabs-link-before
            background: url('../assets/img/svg/account_tabs/tab_svg_4.svg') center center no-repeat

      &:nth-child(5)
        .tabs-component-tab-a
          &:before
            @include tabs-link-before
            background: url('../assets/img/svg/account_tabs/tab_svg_5.svg') center center no-repeat

      &:nth-child(6)
        .tabs-component-tab-a
          &:before
            @include tabs-link-before
            background: url('../assets/img/svg/account_tabs/tab_svg_6.svg') center center no-repeat

      &:nth-child(7)
        .tabs-component-tab-a
          &:before
            @include tabs-link-before
            background: url('../assets/img/svg/account_tabs/tab_svg_7.svg') center center no-repeat
    &-page
      display: flex
      justify-content: space-around
      padding: 0
      margin-top: 20px

  // -- Blocks

  /deep/ .block
    display: flex
    flex-direction: column
    position: relative
    padding: 15px 20px 15px
    background: white
    border-radius: 16px
    border: 1px solid #E0E0E0
    /*overflow: hidden*/
    text-align: left

    &__left
      //flex-grow: 2
      width: 70%
      margin-right: 9px
      margin-bottom: 30px

    &__right
      //flex-grow: 1
      width: 30%
      height: 100%
      margin-left: 9px

    &__without-pad
      padding: 0

    &__row
      display: flex
      justify-content: space-between
      margin: 30px 0

    &__title
      display: inline-block
      margin-bottom: 11px
      font-weight: 500
      text-transform: uppercase
      color: $colorBlockTitle

    &__blackTitle
      font-size: 16px
      font-weight: 500
      color: #000521
      margin: 33px 0 12px

    &__titleLine
      height: 1px
      background: rgb(243, 244, 245)
      margin-left: -20px
      margin-right: -20px
      
    &__separator
      height: 1px
      width: 100%
      background: rgba(224, 224, 224, 0.5)
      &.full-width
        width: 110%
        left: -20px

    &__line
      height: 1px
      width: 100%
      background: rgba(224, 224, 224, 0.5)
</style>
